 /**
  * Component: Editor
  */

var gds = gds || {};

gds.editor = gds.editor || function () {
    this.init = function() {
        lineNumbers();
    };

    /**
     * Adds line numbers to the left side of the page
     */
    this.lineNumbers = function() {
        // Create new div for the line numbers
        var lineNumbersContainer = document.createElement('div');

        // Add class for styling
        lineNumbersContainer.className = 'line-numbers';

        // Append it to the body
        document.body.appendChild(lineNumbersContainer);

        // Calculate the amount of lines, and then add one for each time we have 21 (the base line height)
        var AmountOfLines = lineNumbersContainer.clientHeight / 21;

        for (var i = AmountOfLines - 1; i >= 0; i--) {
            // Create the element
            var line = document.createElement('span');

            // Set the class
            line.className = 'line-numbers__line';

            // Append it to the line numbers container
            lineNumbersContainer.appendChild(line);
        }
    };


    return {
        init: init
    };
}();
